import { Routes, Route, useParams } from 'react-router-dom';
import React, { Component } from 'react';

export default function Section() {
  var { res } = useParams();
  res = res.toLowerCase();
  var resources = [];
  fetch(`${process.env.PUBLIC_URL}/data/Resources.json`)
      .then((response) => {
        return response.json();
      })
      .then((json) => {
         resources = json.Resources;
         console.log(resources);
      });
  if (resources.length == 0)
  {
    return;
  }
  if (!resources.includes(res)) //section doesnt exist
  {
    window.location.href = "https://mathinfo.org/";
    return;
  }
  //const section = (`data/sections/${res}.json`).then(response => { response.json() }).Resources;
  return (
    <div>
      <p>{res}</p>
    </div>
  );
}